*, :after, :before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

html:focus-within {
  scroll-behavior: smooth;
}

body {
  font-size: 1.5rem;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  display: inline-block;
}

input, button, textarea, select {
  font: inherit;
}

button {
  cursor: pointer;
  border: none;
}

.container {
  width: min(90%, 1200px);
  margin: 0 auto;
}

.btn {
  min-width: max-content;
  color: #fff;
  background-color: #000;
  border-radius: .5rem;
  padding: 1rem;
  transition: all .2s ease-in-out;
}

.btn:hover, .btn:active {
  color: #000;
  background-color: #fff;
  box-shadow: 0 0 0 1px #000;
}

.btn--secondary {
  color: #000;
  background-color: #0000;
  box-shadow: 0 1px #000;
}

.btn--secondary:hover, .btn--secondary:active {
  box-shadow: 0 0 0 1px #000;
}

.app {
  min-height: 100vh;
  background-color: #e9e9e9;
}

.header {
  color: #fff;
  text-align: center;
  background-color: #202020;
  padding: 2rem;
}

.header__title {
  color: #fff;
}

.search-params {
  padding-block: 5rem;
}

.form {
  width: min(90%, 600px);
  background-color: #fff;
  border-radius: 1.5rem;
  flex-direction: column;
  margin: 0 auto;
  padding: 3rem;
  display: flex;
  box-shadow: 0 0 0 2px #000;
}

.form__label {
  flex-direction: column;
  margin-bottom: 2rem;
  display: flex;
}

.form__item {
  background-color: #fff;
  border: none;
  border-radius: 1rem;
  margin-top: .5rem;
  padding: 1.5rem;
  box-shadow: 0 0 0 1px #878787;
}

.form__item:disabled {
  box-shadow: 0 0 0 1px #d8d8d8;
}

.form__selects {
  grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
  gap: 2rem;
  display: grid;
}

@media only screen and (max-width: 533px) {
  .form__selects {
    gap: 1rem;
  }
}

.form__btn {
  letter-spacing: .3rem;
  text-transform: uppercase;
  color: #fff;
  background-color: #000;
  border-radius: 1rem;
  margin-top: 2rem;
  padding-block: 1.5rem;
  font-weight: bold;
  transition: all .25s ease-in-out;
}

.form__btn:hover {
  color: #000;
  background-color: #fff;
  box-shadow: 0 0 0 2px #000;
}

.results {
  grid-template-columns: repeat(auto-fit, minmax(min(400px, 90%), 1fr));
  gap: 2rem;
  padding-block: 5rem;
  display: grid;
}

.pet-card {
  background-color: #fff;
  border-radius: 1.5rem;
  gap: 2rem;
  padding: 2rem;
  display: flex;
}

.pet-card__img {
  width: 150px;
  border-radius: 4.5rem;
}

.pet-card__content {
  flex-direction: column;
  align-items: flex-start;
  padding-block: 2rem;
  display: flex;
}

.pet-card__title {
  margin-bottom: .5rem;
}

.pet-card__more {
  color: #2c2c2c;
  border-radius: .5rem;
  margin-top: 1.5rem;
  padding: .5rem 1rem;
  transition: all .2s ease-in-out;
  box-shadow: 0 0 0 1px #2c2c2c;
}

.pet-card__more:hover {
  color: #fff;
  background-color: #2c2c2c;
}

.detail {
  background-color: #fff;
  border-radius: 1.5rem;
  margin-block: 5rem;
  padding: 4rem;
}

.detail__header {
  align-items: center;
  gap: 2rem;
  display: flex;
}

.detail__title {
  font-size: 4rem;
}

.detail__line {
  width: 100%;
  border: 1px dashed #444;
}

.detail__desc {
  color: #333;
  margin-top: 1rem;
  line-height: 1.7;
}

.carousel {
  grid-template-columns: 500px 1fr;
  place-items: flex-end center;
  gap: 2rem;
  margin-bottom: 2rem;
  display: grid;
}

.carousel__big {
  height: 430px;
  display: flex;
}

.carousel__big > img {
  border-radius: 1rem;
}

@media only screen and (max-width: 900px) {
  .carousel {
    grid-template-columns: 1fr;
  }

  .carousel__big {
    height: unset;
  }
}

.carousel__smallers {
  flex-wrap: wrap-reverse;
  justify-content: center;
  justify-self: start;
  gap: 1rem;
  display: flex;
}

@media only screen and (max-width: 900px) {
  .carousel__smallers {
    flex-wrap: wrap;
  }

  .carousel__small {
    width: 70px;
  }
}

.carousel__small {
  cursor: pointer;
  width: 110px;
  filter: grayscale();
  border-radius: .5rem;
}

@media only screen and (max-width: 900px) {
  .carousel__small {
    width: 70px;
  }
}

.carousel__small--active {
  filter: grayscale(0);
}

.modal {
  width: 100%;
  min-height: 100vh;
  z-index: 1;
  background-color: #0009;
  place-items: center;
  display: grid;
  position: absolute;
}

.modal__box {
  text-align: center;
  width: min(500px, 80%);
  background-color: #fff;
  border-radius: 2rem;
  padding: 4rem;
}

.modal__btns {
  justify-content: center;
  gap: 1rem;
  margin-top: 3rem;
  display: flex;
}

.skeleton {
  z-index: 1000;
  background-color: #ebebeb;
  border-radius: .5rem;
  margin: 1rem 0;
}

.skeleton.subtitle {
  width: 40%;
  height: 1.2rem;
}

.skeleton.subtitle--long {
  width: 60%;
  height: 1.2rem;
}

.skeleton.text {
  width: 100%;
  height: 1.2rem;
}

.skeleton.text--last {
  width: 80%;
  height: 1.2rem;
}

.skeleton.subtitle + .skeleton.text, .skeleton.text--last + .skeleton.text {
  margin-top: 2rem;
}

.skeleton.title {
  width: 30%;
  height: 2rem;
  background: #e3e3e3;
  margin-bottom: 1.5rem;
}

.skeleton.img {
  width: 15rem;
  height: 15rem;
}

.skeleton.img--circle {
  border-radius: 100rem;
}

.skeleton.img--hero {
  width: min(350px, 100%);
  height: 350px;
  margin-bottom: 4rem;
}

.skeleton.btn {
  width: 10rem;
  background-color: #dbdbdb;
  margin-top: 3rem;
  padding: 1.6rem;
}

.postcard-skeleton {
  background-color: #fff;
  border-radius: 1rem;
  align-items: center;
  gap: 3rem;
  margin-bottom: 1rem;
  padding: 2rem;
  display: flex;
  position: relative;
  overflow: hidden;
}

.postcard-skeleton > div {
  width: 75%;
}

.postcard-skeleton:before {
  content: "";
  width: 40%;
  opacity: .4;
  background-color: #fff;
  animation: .5s infinite alternate reflect;
  position: absolute;
  top: 0;
  bottom: 0;
  left: -20rem;
  transform: skewX(-10deg);
}

@keyframes reflect {
  from {
    left: -30rem;
  }

  to {
    left: 70rem;
  }
}

.loading {
  justify-content: center;
  gap: 1rem;
  display: flex;
}

.loading-item {
  width: 10px;
  height: 10px;
  background-color: #6b6b6b;
  border-radius: 100%;
}

.loading-item-1 {
  animation: .5s infinite alternate loading;
}

.loading-item-2 {
  animation: .5s .15s infinite alternate loading;
}

.loading-item-3 {
  animation: .5s .3s infinite alternate loading;
}

@keyframes loading {
  from {
    transform: scale(1);
  }

  to {
    transform: scale(1.4);
  }
}

